#statistic {
    margin: auto;
    text-align: center;
    font-size: 50px;
    margin-top: 150px;
    font-weight: 600;
    /* color: #7b7b7b; */
    color: var(--primary);
    letter-spacing: 10px;
    font-family: "Poppins", sans-serif;
  }
  
  #project_contribution {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(1, auto);
    font-family: "Poppins", sans-serif;
    width: 90%;
    margin: auto;
    gap: 50px;
    padding-top: 100px;
    align-content: center;
    padding-bottom: 200px;
    letter-spacing: 1px;
  }
  .conthead {
    text-align: center;
    /* color:black; */
    color: var(--primary);
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    padding-top: 30px;
    font-size: 20px;
    padding-bottom: 20px;
    transition: all 1s;
  }
  .conthead:hover {
    transform: rotateY(360deg);
  }
  .conthead > h2 {
    font-size: 30px;
    font-weight: 700;
  }
  .conthead > h2 > span {
    color: var(--primary);
    font-size: 40px;
  }
  .conthead > h2 + p {
    font-weight: 500;
  }
  @media all and (min-width: 500px) and (max-width: 940px) {
    #statistic {
      font-size: 40px;
      margin-top: 100px;
      font-weight: 700;
      letter-spacing: 7px;
    }
    #project_contribution {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(3, auto);
    }
    
  }
  @media all and (min-width: 150px) and (max-width: 500px) {
    #statistic {
      font-size: 40px;
      margin-top: 60px;
      font-weight: 700;
      letter-spacing: 5px;
      padding-top: 70px;
    }
    #project_contribution {
      display: grid;
      grid-template-columns: repeat(1, 1fr);
      grid-template-rows: repeat(5, auto);
      padding-top: 50px;
    }
  }
  .git{
    width: 80%;
    margin: auto;
    color: var(--primary);
  }
  .git span{
    color: var(--primary);
  }
  .calendar
  {
    margin-top: -200px;
  }